eval("\n\nwindow.addEventListener('DOMContentLoaded', function () {\n  //Form\n  var nextBtn = document.querySelector('.body-form__btn_next');\n  var backBtn = document.querySelector('.body-form__btn_back');\n  var formBlocks = document.querySelectorAll('.body-form__block');\n  nextBtn.addEventListener('click', function () {\n    formBlocks[0].style.display = 'none';\n    formBlocks[1].style.display = 'block';\n  });\n  backBtn.addEventListener('click', function () {\n    formBlocks[1].style.display = 'none';\n    formBlocks[0].style.display = 'block';\n  }); //Header-On-Scroll\n\n  var header = document.querySelector('.header');\n\n  function headerActive() {\n    if (window.scrollY > 0) {\n      header.classList.add('active');\n    } else {\n      header.classList.remove('active');\n    }\n  }\n\n  window.addEventListener('scroll', headerActive); //Dinamic Adaptive\n\n  var registrationBtn = document.querySelector('.actions-header__registration');\n  var langBtns = document.querySelector('.actions-header__lang');\n  var loginBtn = document.querySelector('.actions-header__login');\n  var headerMenu = document.querySelector('.header__menu');\n  var headerBurger = document.querySelector('.actions-header__burger');\n  var symbolLabel = document.querySelector('.body-form__label_symbol');\n  var confirmLabel = document.querySelector('.body-form__label_confirm');\n  var symbolImg = document.querySelector('.body-form__symbols-img');\n\n  function dinamicAdaptiv() {\n    var viewportWidth = Math.max(document.documentElement.clientWidth, window.innerWidth);\n\n    if (viewportWidth <= 1200 && viewportWidth >= 768) {\n      if (symbolLabel) {\n        confirmLabel.after(symbolLabel);\n      }\n    } else {\n      if (symbolLabel) {\n        symbolImg.after(symbolLabel);\n      }\n    }\n\n    if (viewportWidth <= 992) {\n      if (registrationBtn) {\n        headerMenu.append(registrationBtn);\n      }\n    } else {\n      if (registrationBtn) {\n        headerBurger.before(registrationBtn);\n      }\n    }\n\n    if (loginBtn) {\n      if (viewportWidth <= 600) {\n        registrationBtn.after(loginBtn);\n      } else {\n        langBtns.after(loginBtn);\n      }\n    }\n  }\n\n  window.addEventListener('resize', dinamicAdaptiv);\n  window.addEventListener('load', function () {\n    dinamicAdaptiv();\n    headerActive();\n  }); //Burger Menu\n\n  function burgerMenu(btnSelector, bodySelector, itemsSelector) {\n    var btn = document.querySelector(btnSelector);\n    var body = document.querySelector(bodySelector);\n    var items = document.querySelectorAll(itemsSelector);\n    btn.addEventListener('click', function () {\n      btn.classList.toggle('active');\n      body.classList.toggle('active');\n      var ariaLabel = btn.getAttribute('aria-label');\n\n      if (ariaLabel === 'Открыть меню') {\n        btn.setAttribute('aria-label', 'Закрыть меню');\n      } else {\n        btn.setAttribute('aria-label', 'Открыть меню');\n      }\n    });\n    items.forEach(function (item) {\n      item.addEventListener('click', function () {\n        body.classList.toggle('active');\n        btn.classList.toggle('active');\n      });\n    });\n  }\n\n  if (document.querySelector('.actions-header__burger')) {\n    burgerMenu('.actions-header__burger', '.header__menu', '.header__item');\n  }\n\n  document.addEventListener('click', function (e) {\n    var burgerBtn = document.querySelector('.actions-header__burger');\n    var target = e.target;\n\n    if (!target.closest('.header__menu') && !target.classList.contains('actions-header__burger') && !target.classList.contains('actions-header__burger-span')) {\n      if (headerMenu.classList.contains('active')) {\n        headerMenu.classList.remove('active');\n      }\n\n      if (burgerBtn.classList.contains('active')) {\n        burgerBtn.classList.remove('active');\n      }\n    }\n  });\n});\n\n//# sourceURL=webpack://gulpstarter/./src/js/script.js?");

